import {createStore} from 'vuex'
import router from "@/router";
import AxiosUtil from "@/utils/AxiosUtil";
import {ElMessage} from "element-plus";

export default createStore({
    state: {
        editableTabs: [
            {
                title: '首页',
                name: '/index'
            }
        ],
        editableTabsValue: '/index'
    },
    getters: {
        GET_TOKEN: () => {
            console.log('store get token= ', sessionStorage.getItem('token'));
            return sessionStorage.getItem('token');
        },
        GET_MENULIST: () => {
            // console.log('store get menuList= ', sessionStorage.getItem('menuList'));
            return JSON.parse(sessionStorage.getItem('menuList'));
        },
        GET_PERMS: () => {
            // console.log('store get perms= ', sessionStorage.getItem('perms'));
            return JSON.parse(sessionStorage.getItem('perms'));
        },
        GET_USERINFO: () => {
            // console.log('store get userInfo= ', sessionStorage.getItem('userInfo'));
            return JSON.parse(sessionStorage.getItem('userInfo'));
        },
    },
    mutations: {
        SET_TOKEN: (state, token) => {
            console.log('store set token= ', token);
            sessionStorage.setItem('token', token);
        },
        SET_MENULIST: (state, menuList) => {
            // console.log('store set menuList= ', menuList);
            sessionStorage.setItem('menuList', JSON.stringify(menuList));
        },
        SET_PERMS: (state, perms) => {
            // console.log('store set perms= ', perms);
            sessionStorage.setItem('perms', JSON.stringify(perms));
        },
        SET_USERINFO: (state, userInfo) => {
            // console.log('store set userInfo= ', userInfo);
            sessionStorage.setItem('userInfo', JSON.stringify(userInfo));
        },
        RELOAD_ROUTE: () => {
            let menuList = JSON.parse(sessionStorage.getItem('menuList'));
            let copiedRoutes = router.options.routes;
            // 定义菜单转路由的方法
            let menuToRoute = (menu, parentName) => {
                // 只为包含组件的信息的菜单提供转换
                if (menu.component) {
                    let route = {
                        path: menu.path,
                        name: menu.name,
                        component: () => import('@/views/' + menu.component),
                        meta: {
                            parentName: parentName
                        }
                    };
                    // console.log('menuToRoute route= ', route);
                    // route.component = () => import('@/views/' + menu.component);
                    return route;
                }
            };
            // 创建新的路由信息
            menuList.forEach((menu) => {
                // console.log('menu= ', menu);

                // 默认将子菜单转换成路由，如无子菜单再考虑转换一级菜单
                if (menu.children) {
                    menu.children.forEach(menuItem => {
                        let route = menuToRoute(menuItem, menu.name);
                        if (route) {
                            copiedRoutes[0].children.push(route);
                        }
                    });
                } else {
                    let route = menuToRoute(menu, null);
                    if (route) {
                        copiedRoutes[0].children.push(route);
                    }
                }
            });
            // 将新的路由信息添加到路由
            copiedRoutes.forEach(route => {
                router.addRoute(route);
            });
        },
        OPEN_TAB: (state, menu) => {
            console.log('store set openTab= ', menu);

            // 判断传入菜单，是否已存在对应的标签页，如不存在则创建
            // 菜单数据转换成标签页数据，规则：menu.name > tab.title，menu.path > tab.name
            let res = state.editableTabs.findIndex(tab => tab.name === menu.path);
            if (res == -1) {
                let newTab = {
                    title: menu.name,
                    name: menu.path
                };
                state.editableTabs.push(newTab);
                console.log('添加了一个新的标签页', newTab);
            }

            // 将对应的标签页设置为激活的标签页
            state.editableTabsValue = menu.path;

            // 路由至菜单对应的路径
            router.push(menu.path);

            // 将标签页列表保存到 sessionStorage
            sessionStorage.setItem('editableTabs', JSON.stringify(state.editableTabs));
            // 将激活的标签页保存到 sessionStorage
            sessionStorage.setItem('editableTabsValue', JSON.stringify(state.editableTabsValue));
        },
        // 从 sessionStorage 中重载标签页数据
        RELOAD_TAB: (state) => {
            let tabs = sessionStorage.getItem('editableTabs');
            if (tabs) {
                state.editableTabs = JSON.parse(tabs);
            }
            let activeTab = sessionStorage.getItem('editableTabsValue');
            if (activeTab) {
                state.editableTabsValue = JSON.parse(activeTab);
            }
        },
        RESET_TAB: (state) => {
            state.editableTabs = [
                {
                    title: '首页',
                    name: '/index'
                }
            ];
            state.editableTabsValue = '/index';
        },
    },
    actions: {
        logout: async (context) => {
            let logoutStatus = false;

            // 尝试在服务器上登出
            try {
                let res = await AxiosUtil.get('/logout');
                if (res.data.code == 200) {
                    logoutStatus = true;
                    ElMessage({
                        message: res.data.msg,
                        type: 'success',
                    });
                }
            } catch (e) {
                console.log('logout e=', e);
                if (confirm('在服务器登出时出现问题，\n是否在本地退出登录？')) {
                    logoutStatus = true;
                    ElMessage({
                        message: '已在本地退出登录',
                        type: 'warning',
                    });
                }
            }

            // 如已登出，则删除登录数据、标签页数据并跳转到登录页
            if (logoutStatus) {
                sessionStorage.clear();
                context.commit('RESET_TAB');
                // location.reload();
                await router.replace({path: '/login'});
            }
        }
    },
    modules: {}
})
