<template>
  <el-tabs
      v-model="editableTabsValue"
      type="card"
      class="demo-tabs"
      closable
      @tab-click="clickTab"
      @tab-remove="removeTab"
      onselectstart="return false;"
  >
    <el-tab-pane
        v-for="item in editableTabs"
        :key="item.name"
        :label="item.title"
        :name="item.name"
    >
      <!--      {{ item.content }}-->
    </el-tab-pane>
  </el-tabs>
</template>
<script setup>
import {computed} from 'vue'
import store from '@/store';
import router from "@/router";
import {useRoute} from "vue-router";

// 定义当前路由
const route = useRoute();

// 标签页列表
const editableTabs = computed({
  // getter
  get() {
    return store.state.editableTabs;
  },
  // setter
  set(newValue) {
    store.state.editableTabs = newValue;
    // 将标签页列表保存到 sessionStorage
    sessionStorage.setItem('editableTabs', JSON.stringify(newValue));
  }
});

// 当前激活的标签页
const editableTabsValue = computed({
  // getter
  get() {
    // return route.path;
    return store.state.editableTabsValue;
  },
  // setter
  set(newValue) {
    store.commit('OPEN_TAB', {path: newValue});
  }
});

// 点击标签页
const clickTab = (tab) => {
  console.log(tab.props.name);
  console.log('routes= ', router.options.routes);
  // router.push(tab.props.name);
  let menu = {
    name: tab.props.label,
    path: tab.props.name,
  };
  console.log('clickTab menu=', menu);
  store.commit('OPEN_TAB', menu);
}

// 移除标签页
const removeTab = (targetName) => {
  // 首页不能关闭
  console.log('关闭标签页：', targetName);
  if (targetName === '/index') {
    return;
  }

  // 获取标签页列表，以及激活的标签页
  const tabs = editableTabs.value;
  let activeName = editableTabsValue.value;

  // 如果处于激活状态的标签页，为要关闭的标签页，则令其为下一标签页（若不存在则令其为上一标签页）
  if (activeName === targetName) {
    tabs.forEach((tab, index) => {
      if (tab.name === targetName) {
        const nextTab = tabs[index + 1] || tabs[index - 1];
        if (nextTab) {
          activeName = nextTab.name;
        }
      }
    })
  }

  // 更新激活状态的标签页
  editableTabsValue.value = activeName;
  // 更新标签页列表，排除要关闭的标签页
  editableTabs.value = tabs.filter((tab) => tab.name !== targetName);
}

</script>

<script>
export default {
  name: "Tabs"
}
</script>

<style scoped>
.demo-tabs > .el-tabs__content {
  padding: 32px;
  color: #6b778c;
  font-size: 32px;
  font-weight: 600;
}
</style>
