// 引入axios
import axios from 'axios';
// 引入store
import store from '@/store/index'
// 引入router
import router from "@/router";
// 引入ElMessage
import {ElNotification} from 'element-plus'

let baseUrl = 'http://localhost:97';

// 创建axios实例
// https://axios-http.com/docs/instance
const axiosInstance = axios.create({
    // baseURL: 'https://some-domain.com/api/',
    baseURL: baseUrl,
    // timeout: 2500,
    timeout: 10000,
    // headers: {'X-Custom-Header': 'foobar'}
});

// https://axios-http.com/docs/interceptors
// 添加请求拦截器
axiosInstance.interceptors.request.use(function (config) {
    // 在请求发送之前做些什么
    let token = store.getters.GET_TOKEN;
    // console.log('token= ', token);
    config.headers.token = token;
    return config;
}, function (error) {
    // Do something with request error
    return Promise.reject(error);
});

// 添加响应拦截器
axiosInstance.interceptors.response.use(function (response) {
    // Any status code that lie within the range of 2xx cause this function to trigger
    // Do something with response data
    console.log('响应成功！response.status=', response.status);
    return response;
}, function (error) {
    // Any status codes that falls outside the range of 2xx cause this function to trigger
    // Do something with response error
    console.log('响应出现问题！error=', error);
    console.log('响应出现问题！error.message=', error.message);
    console.log('响应出现问题！error.response=', error.response);
    let msg = '<' + error.code + '> :: ' + error.message;
    let res = error.response;
    if (res) msg = '<' + res.data.code + '> :: ' + res.data.msg;
    ElNotification({
        title: error.name,
        message: '哎呀呀！出现了点意外：' + msg,
        type: 'error',
        duration: 6000
    });
    if (res && res.data.code == 401) {
        // 删除sessionstorage，并跳转到登录页
        console.log('跳转到登录页');
        router.push({path: '/relogin'});
    }
    return Promise.reject(error);
});

// 网络请求方法

export function get(url, params = {}) {
    return new Promise((resolve, reject) => {
        axiosInstance({
            url: url,
            method: 'get',
            params: params
        }).then(response => {
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
}


export function del(url, params = {}) {
    return new Promise((resolve, reject) => {
        axiosInstance({
            url: url,
            method: 'delete',
            params: params
        }).then(response => {
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
}


export function post(url, data = {}) {
    return new Promise((resolve, reject) => {
        axiosInstance({
            url: url,
            method: 'post',
            data: data
        }).then(response => {
            console.log('post response= ', response);
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
}


export function put(url, data = {}) {
    return new Promise((resolve, reject) => {
        axiosInstance({
            url: url,
            method: 'put',
            data: data
        }).then(response => {
            console.log('put response= ', response);
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
}


export function fileUpload(url, data = {}) {
    return new Promise((resolve, reject) => {
        axiosInstance({
            url: url,
            method: 'post',
            data: data,
            headers: {'Content-Type': 'multipart/form-data'}
        }).then(response => {
            resolve(response);
        }).catch(error => {
            reject(error);
        });
    });
}

export function getServerUrl() {
    return baseUrl;
}

export default {
    get,
    del,
    post,
    put,
    fileUpload,
    getServerUrl
}

