import {createApp} from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
// 引入ElementPlus
import ElementPlus from 'element-plus'
import 'element-plus/dist/index.css'
// 引入ElementPlus图标
import * as ElementPlusIconsVue from '@element-plus/icons-vue'
// ElementPlus国际化
import zhCn from 'element-plus/dist/locale/zh-cn.mjs'
// 引入权限检查工具
import {mixinFunction} from '@/utils/AuthorityChecker';


const app = createApp(App)

// 注册所有ElementPlus图标
for (const [key, component] of Object.entries(ElementPlusIconsVue)) {
    app.component(key, component)
}

// 全局前置守卫
router.beforeEach((to, from, next) => {
    // to 即将要进入的目标
    // from 当前导航正要离开的路由

    console.log("++++++++收到路由请求 to.path= " + to.path + ", from.path= " + from.path);

    // TODO 可试图更改为如下方案：
    // 创建一个白名单数组
    // 判断是否携带token/拥有资源的访问权限
    // 判断目的地是否匹配白名单

    // 修改网站标题
    document.title = (to.name || '欢迎') + ' - ' + (process.env.VUE_APP_TITLE || '(●\'◡\'●)'); // TODO 有可能跳转会被拦截，考虑该情况下是否需要设置标题。

    // 从 sessionStorage 中重载标签页列表、激活的标签页
    store.commit('RELOAD_TAB');

    // 从 sessionStorage 中获取token
    const token = sessionStorage.getItem('token');

    // TODO bug.手动在浏览器地址栏进入登录页，回到首页，但激活的标签页却仍是之前的标签页而非首页。可考虑对记录上一个可用的地址以供备用。
    // TODO bug.跳转到一个不存在的位置时，比如 /sys，会导致无限尝试从而卡死。
    // 访问登录页面时：如已登录（即存在token时），则路由到首页 /
    if (to.path === '/login' || to.name === 'Login') {
        if (token) {
            console.log("你已登录，无需再次登录！");
            next({path: '/'});
        } else {
            // 放行
            next();
        }
    }
    // 访问非登录页面时：如未登录（即不存在token时），则路由到登录页 /login
    else {
        if (!token) {
            console.log("无token数据，请先登录！");
            next({path: '/login'});
        } else {
            // 刷新浏览器后，路由数据不完整。需先重新从 sessionStorage 中加载路由数据，再进入路由。
            if (!router.hasRoute(to.name)) {
                store.commit('RELOAD_ROUTE');
                next({path: to.path});
                // next();
                // console.log('routes=', router.options.routes);
                // if (!router.hasRoute(to.name)) {
                //     next({path: '/'});
                // } else {
                //     next({path: to.path});
                // }
            } else {
                // 放行
                next();
            }
        }
    }
});

// https://nodejs.org/api/process.html#processenv
// process.env 属性返回一个包含用户环境的对象。
// https://v3.cli.vuejs.org/zh/guide/mode-and-env.html
// 只有以 VUE_APP_ 开头的变量会被 webpack.DefinePlugin 静态嵌入到客户端侧的包中。
console.log('process.env=', process.env);
console.log('process.env.VUE_APP_PUBLIC_PATH= ' + process.env.VUE_APP_PUBLIC_PATH);

app.use(ElementPlus, {
    locale: zhCn,
}).use(store).use(router).mixin(mixinFunction).mount('#app');
