<template>
  <el-scrollbar>
    <el-menu
        active-text-color="#ffd04b"
        background-color="#545c64"
        text-color="#fff"
        @open="handleOpen"
        @close="handleClose"
        @select="handleSelect"
        :default-active="activeAsideMenu"
        :default-openeds="['/sys', '/bsns', '/center', '/outpatient']"
    >

<!--      <el-menu-item index="/index">-->
<!--        <template #title>-->
<!--          <el-icon><HomeFilled /></el-icon>-->
<!--          首页-->
<!--        </template>-->
<!--      </el-menu-item>-->
      <template v-for="menu in menuData" :key="menu.path">
        <!--功能导航菜单项，其各自对应的路由path值应当是不冲突的，因此选用其path值作为菜单的index值，以及v-for的key值-->
        <el-sub-menu v-if="menu.children!=undefined&&menu.children.length>0" :index="menu.path">
          <template #title>
            <el-icon><component v-if="menu.icon!=''" :is="menu.icon"/></el-icon>
            {{ menu.name }}
          </template>
          <template v-for="menuItem in menu.children" :key="menuItem.path">
            <el-menu-item :index="menuItem.path" @click="selectMenu(menuItem)">
              <template #title>
                <el-icon><component v-if="menuItem.icon!=''" :is="menuItem.icon"/></el-icon>
                {{ menuItem.name }}
              </template>
            </el-menu-item>
          </template>
        </el-sub-menu>
        <el-menu-item v-else :index="menu.path" @click="selectMenu(menu)">
          <template #title>
            <el-icon><component v-if="menu.icon!=''" :is="menu.icon"/></el-icon>
            {{ menu.name }}
          </template>
        </el-menu-item>
      </template>

    </el-menu>
  </el-scrollbar>
</template>

<script setup>

import {computed, ref} from "vue";
import store from '@/store';
// import router from "@/router";
import {useRoute} from "vue-router";

const route = useRoute();

// 当前激活的菜单
const activeAsideMenu = computed(() => {
  return route.path;
});


// sub-menu 展开、收起的回调
const handleOpen = (key, keyPath) => {
  console.log('菜单展开', key, keyPath)
}
const handleClose = (key, keyPath) => {
  console.log('菜单收起', key, keyPath)
}
// 选择菜单的处理事件
const handleSelect = (index, indexPath, item, routeResult) => {
  console.log('选择菜单');
  console.log('index= ', index); //index: 选中菜单项的 index
  console.log('indexPath= ', indexPath); //indexPath: 选中菜单项的 index path
  console.log('item= ', item); //item: 选中菜单项
  console.log('routeResult= ', routeResult); //routeResult: vue-router 的返回值（如果 router 为 true）
}

// 选择菜单，打开对应标签页
const selectMenu = (menu) => {
  console.log('selectMenu ', menu);
  store.commit('OPEN_TAB', menu);
}
</script>

<script>
export default {
  name: "Aside",
  props: ['menuData'],
}
</script>

<style scoped>

</style>
