import store from '@/store';

export const mixinFunction = {
    methods: {
        // 检查当前用户是否具备某些权限，如具备其中一个则返回 true
        hasAnyAuthority() {
            let userPerms = store.getters.GET_PERMS;
            // console.log('arguments=', arguments);
            // console.log('userPerms=', userPerms);
            for (const perm of arguments) {
                for (const userPerm of userPerms) {
                    if (userPerm == perm) return true;
                }
            }
            return false;
        }
    }
}