import {createRouter, createWebHistory, createWebHashHistory} from 'vue-router'
import HomeView from '../views/HomeView.vue'

const routes = [
    {
        path: '/',
        name: 'Home',
        component: HomeView,
        redirect: {path: '/index'},
        children: [
            {
                path: '/index',
                name: '首页',
                // meta: {title: '首页'},
                component: () => import('@/views/index/Index')
            },
            {
                path: '/testPage',
                name: 'TestPage',
                component: () => import('@/views/test/TestPage')
            },
            {
                path: '/relogin',
                name: 'LoginDialog',
                component: () => import('@/views/LoginDialog')
            },
        ]
    },
    {
        path: '/login',
        name: 'Login',
        // meta: {title: '登录页'},
        component: () => import('@/views/Login'),
    },
    {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '@/views/AboutView')
    },
]

const router = createRouter({
    // history: createWebHistory(process.env.BASE_URL),
    history: (process.env.VUE_APP_HASH_MODE === 'Y') ? createWebHashHistory(process.env.BASE_URL) : createWebHistory(process.env.BASE_URL),
    // history: process.env.NODE_ENV === 'production' ? createWebHashHistory(process.env.BASE_URL) : createWebHistory(process.env.BASE_URL), // TODO 当服务器不支持代理时，只能用带#号的Hash模式
    routes
})

export default router
